import androidx.compose.runtime.Composable
import androidx.compose.ui.Modifier
import kotlinx.browser.window
import ui.patient.newpatient.NewPatientComponent
import ui.patient.newpatient.NewPatientContent
import ui.patient.newpatient.NewPatientEvent

@Composable
fun WebNewPatientContent(
    component: NewPatientComponent,
    modifier: Modifier = Modifier
) {
    NewPatientContent(
        openCamera = {
            val byteArray: Array<Int> = window.asDynamic().takePicture()
            val byteBuffer = ByteArray(byteArray.size)
            for (i in byteArray.indices) {
                byteBuffer[i] = byteArray[i].toByte()
            }
            component.handleEvent(NewPatientEvent.OnUpdateCameraImage(byteBuffer))
        },
        component = component,
        modifier = modifier
    )
}