        val a = Employee(
            id = 0L,
            nfc = "3116900580",
            name = "Juan",
            salary = 2
        )
        val s: MutableList<Employee> = mutableListOf()
        repeat(100) {
            s.add(
                a.copy(
                    id = it.toLong(),
                    name = "juan $it"
                )
            )
        }

        return s
