import androidx.compose.foundation.layout.Column
import androidx.compose.foundation.layout.fillMaxWidth
import androidx.compose.material3.Button
import androidx.compose.material3.MaterialTheme
import androidx.compose.runtime.getValue
import androidx.compose.runtime.mutableStateOf
import androidx.compose.runtime.setValue
import androidx.compose.ui.Alignment
import androidx.compose.ui.ExperimentalComposeUiApi
import androidx.compose.ui.Modifier
import androidx.compose.ui.window.CanvasBasedWindow
import com.arkivanov.decompose.DefaultComponentContext
import com.arkivanov.essenty.lifecycle.LifecycleRegistry
import com.arkivanov.essenty.lifecycle.resume
import common.EmptyLayout
import di.appModule
import di.createPatientComponent
import kotlinx.browser.document
import kotlinx.browser.window
import org.jetbrains.skiko.wasm.onWasmReady
import org.koin.core.context.startKoin
import ui.patient.newpatient.NewPatientComponent
import ui.patient.newpatient.NewPatientContent

@OptIn(ExperimentalComposeUiApi::class)
fun main() {
    onWasmReady {
        startKoin {
            modules(appModule())
        }

        var isAdded by mutableStateOf(false)

        val lifecycle = LifecycleRegistry()
        val root = createPatientComponent(
            onSuccess = {
                isAdded = true
            },
            componentContext = DefaultComponentContext(lifecycle = lifecycle)
        )

        lifecycle.resume()

        CanvasBasedWindow("Timetap") {
            if (isAdded) {
                EmptyLayout(
                    message = "Successfully added.",
                    style = MaterialTheme.typography.displayLarge
                )
            } else {
                WebNewPatientContent(
                    component = root
                )
            }
        }

        document.getElementById("loader-container")?.remove()
    }
}